$g-width: 25%;
$g-height: 298px;

.Grid {
  display: grid;
  grid-template-rows: $g-height $g-height $g-height;
  grid-template-columns: $g-width $g-width $g-width $g-width;
  color: rgba(51, 51, 51, 1);

  & > * {
    // border: 1px solid black;
    overflow: hidden;
  }
}

.title {
  font-size: 32px;
  font-weight: 700;
}

.text {
  line-height: 26px;
  font-weight: 500;
}

.g2, .g5, .g7 {
  padding: 48px 24px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.g1 {
  grid-column: 1 / span 2;
  grid-row: 1 / span 2;

  img {
    object-fit: cover;
  }
}

.g2 {
  
}

.g3 {

  img {
    transform: translateX(-20px);
  }
}

.g4 {

  img {
    transform: translateX(-20px);
  }
}

.g5 {

}

.g6 {

  img {
    // transform: translateX(-20px);
  }
}

.g7 {

}

.g8 {
  grid-column: 3 / span 2;

  img {
    transform: translateX(-50px);
    height: 100%;
  }
}
