.Product {
  display: grid;
  grid-template-areas: 
    'header        header'
    'img-container img-container'
    'colors        reviews'
    'price         price';
  padding: 24px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.226);
  border-radius: 28px;
}

.header {
  grid-area: header;
  display: flex;
  flex-direction: column;
  
}

.img-container {
  grid-area: img-container;
  text-align: center;
}

.colors {
  grid-area: colors;
}

.reviews {
  grid-area: reviews;
}

.price {
  grid-area: price;
}

.name {
  font-size: 22px;
  font-weight: 700;
}

.availability {
  font-weight: 500;
  line-height: 26px;

  &.available {
    color: rgba(24, 191, 111, 1);
  }
}

.colors {
  display: flex;
  flex-direction: column;
  font-weight: 500;
  gap: 8px;
  color: rgba(154, 154, 154, 1);
  font-size: 14px;
}

.colors-list {
  display: flex;
  gap: 8px;
}

.colors-item {
  border-radius: 50%;
  width: 16px;
  height: 16px;
}

.reviews {
  display: flex;
  flex-direction: column;
  gap: 7px;
  justify-content: center;
  align-items: flex-end;
  font-weight: 500;
  font-size: 12px;
  color: rgba(154, 154, 154, 1);
}

.price {
  margin-top: 24px;
  font-size: 24px;
  color: rgba(225, 35, 80, 1);
  font-weight: 700;
}

.stars {
  display: flex;
  grid: 7px;
}

.star {
  width: 13px;
  height: 13px;
}
