.Intro {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 133px;
  background-color: #131218;
  color: white;
}

.left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.right {

}

.title {
  white-space: pre-line;
  font-size: 40px;
  font-weight: 600;
  line-height: 48px;
}

.about-text {
  margin-top: 24px;
  white-space: pre-line;
  line-height: 26px;
  font-weight: 500;
  letter-spacing: .4px;
}

.buttons {
  margin-top: 43px;
  display: flex;
  gap: 43px;
  align-items: center;
}

.contact-btn {
  color: white;
  padding: 16px 66px;
  border-radius: 28px;
  background-color: rgba(24, 104, 224, 1);
  font-size: 18px;
  font-weight: 500;
}

.location {
  display: flex;
  align-items: center;
  gap: 11px;
  color: rgba(24, 104, 224, 1);
  font-weight: 500;
}

