@import './base';

*, *::after, *::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: RedHatDisplay;
  src: url(../assets/fonts/RedHatDisplay-Regular.ttf);
}

html {
  font-size: 16px;
  font-family: RedHatDisplay;
}

input, button {
  outline: none;
  border: none;
  background-color: inherit;
}

a {
  color: inherit;
}

ul {
  list-style-type: none;
}
