.Products {
  padding: 134px 0 240px 0;
  display: flex;
  flex-direction: column;
}

.title {
  text-align: center;
  font-size: 40px;
  font-weight: 700;
  color: rgba(51, 51, 51, 1);
}

.list {
  margin-top: 48px;
  $width: 288px;
  display: grid;
  gap: 20px;
  justify-content: center;
  grid-template-columns: $width $width $width $width;
}
